ty classes\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nspan {\n  @include base-text-rules();\n  vertical-align: baseline;\n}\n\nspan > span {\n  vertical-align: baseline;\n}\n","@import \"../../../styles/variables.scss\";\n\n@mixin text-disabled($property) {\n  pointer-events: none;\n  color: $disabled;\n}\n\n@mixin base-text-rules() {\n  transition-property: color, opacity;\n  transition: 0.2s ease-out;\n  vertical-align: middle;\n\n  &.text-nowrap {\n    white-space: nowrap;\n  }\n  &.text-ellipsis {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n\n  @include text-sizing();\n  @include text-colors();\n  @include text-margins();\n  @include text-lengths();\n  @include text-weights();\n}\n\n@mixin text-sizing() {\n  // Controls font-size of text\n  &.text-size-default {\n    font-size: inherit;\n  }\n  &.text-size-body {\n    font-size: $font-size-body;\n  }\n  &.text-size-xs {\n    font-size: $font-size-xs;\n  }\n  &.text-size-sm {\n    font-size: $font-size-sm;\n  }\n  &.text-size-md {\n    font-size: $font-size-md;\n  }\n  &.text-size-lg {\n    font-size: $font-size-lg;\n  }\n  &.text-size-xl {\n    font-size: $font-size-xl;\n  }\n  &.text-size-2xl {\n    font-size: $font-size-2xl;\n  }\n  &.text-size-3xl {\n    font-size: $font-size-3xl;\n  }\n  &.text-size-smaller {\n    font-size: 0.8em;\n  }\n  &.text-size-larger {\n    font-size: 1.2em;\n  }\n}\n\n@mixin text-weights() {\n  // Font Weight Utils\n  &.text-weight-hairline {\n    font-weight: 100;\n  }\n  &.text-weight-thin {\n    font-weight: 200;\n  }\n  &.text-weight-light {\n    font-weight: 300;\n  }\n  &.text-weight-normal {\n    font-weight: 400;\n  }\n  &.text-weight-medium {\n    font-weight: 500;\n  }\n  &.text-weight-semibold {\n    font-weight: 600;\n  }\n  &.text-weight-bold {\n    font-weight: 700;\n  }\n  &.text-weight-extrabold {\n    font-weight: 800;\n  }\n  &.text-weight-heavy {\n    font-weight: 900;\n  }\n  &.text-weight-lighter {\n    font-weight: lighter;\n  }\n  &.text-weight-bolder {\n    font-weight: bolder;\n  }\n}\n\n@mixin text-lengths() {\n  &.text-length-small {\n    max-width: 40ch;\n  }\n  &.text-length-medium {\n    max-width: 55ch;\n  }\n  &.text-length-large {\n    max-width: 70ch;\n  }\n}\n\n@mixin text-colors() {\n  @include theme-colors() using ($name, $color, $contrast, $tint, $shade, $pale) {\n    &.text-color-#{$name} {\n      color: $color;\n    }\n  }\n}\n\n@mixin text-margins() {\n  &.margin-before {\n    margin-top: 0.4rem;\n  }\n\n  &.margin-after {\n    margin-bottom: 0.8rem;\n  }\n}\n\n@mixin novo-body-text() {\n  display: inline;\n  font-weight: normal;\n  color: inherit;\n  font-size: var(--font-size-text);\n  @include base-text-rules();\n}\n\n@mixin novo-title-text() {\n  font-weight: 500;\n  line-height: 1.5;\n  color: var(--text-main, $dark);\n  white-space: nowrap;\n  text-overflow: ellipsis;\n  font-size: var(--font-size-title);\n  @include base-text-rules();\n}\n\n@mixin novo-label-text() {\n  font-weight: 500;\n  // text-transform: uppercase;\n  word-break: word-break;\n  overflow-wrap: break-word;\n  line-height: 1.375;\n  color: var(--text-muted);\n  font-size: var(--font-size-label);\n  @include base-text-rules();\n}\n\n@mixin novo-caption-text() {\n  font-size: var(--font-size-caption);\n  font-weight: 400;\n  line-height: 1.375;\n  color: var(--text-muted);\n  @include base-text-rules();\n}\n","// Iconography\n// --------------------------------------------------\n$icon-height: 1.7rem; // 24px Base background size\n$icon-width: 1.7rem; // 24px Base background size\n$base-icon-size: 1.4rem; //18px Base font-size\ni {\n  font-size: $base-icon-size;\n  display: inline-table;\n  flex: 0 0 auto;\n  &[theme] {\n    &[theme=\"contained\"] {\n      font-size: 0.8rem;\n      border-radius: 0.55rem;\n      height: $icon-height;\n      width: $icon-width;\n      text-align: center;\n      &:before {\n        vertical-align: baseline;\n        line-height: 1.8;\n      }\n      @each $entity, $