ooltip {\n  div {\n    background: $tooltip-background;\n    color: var(--color-white, #fff);\n    border-radius: var(--tooltip-border-radius, 4px);\n    padding: 8px 10px;\n    font-size: 12px;\n    line-height: 12px;\n    white-space: nowrap;\n    box-shadow: var(--shadow-2);\n\n    &.error {\n      background-color: $tooltip-error-color;\n      @include tooltip-pointer($tooltip-error-color);\n    }\n    &.info {\n      background-color: $tooltip-info-color;\n      @include tooltip-pointer($tooltip-info-color);\n    }\n    &.warning {\n      background-color: $tooltip-warning-color;\n      @include tooltip-pointer($tooltip-warning-color);\n    }\n    &.success {\n      background-color: $tooltip-success-color;\n      @include tooltip-pointer($tooltip-success-color);\n    }\n\n    //Arrow positions\n    @include tooltip-pointer($tooltip-background);\n\n    &.top:before {\n      margin-bottom: -11px;\n      left: calc(50% - 6px);\n      bottom: 0;\n    }\n\n    &.top-left:before {\n      margin-right: 0;\n      margin-bottom: -11px;\n      right: 1px;\n      bottom: 0;\n    }\n\n    &.top-right:before {\n      margin-left: 0;\n      margin-bottom: -11px;\n      left: 1px;\n      bottom: 0;\n    }\n\n    &.bottom:before {\n      margin-top: -11px;\n      left: calc(50% - 6px);\n      top: 0;\n    }\n\n    &.bottom-left:before {\n      margin-right: 0;\n      margin-top: -11px;\n      right: 1px;\n      top: 0;\n    }\n\n    &.bottom-right:before {\n      margin-left: 0;\n      margin-top: -11px;\n      left: 1px;\n      top: 0;\n    }\n\n    &.left:before {\n      margin-right: -11px;\n      margin-bottom: -6px;\n      right: 0;\n      bottom: 50%;\n    }\n\n    &.right:before {\n      left: 0;\n      bottom: 50%;\n      margin-left: -11px;\n      margin-bottom: -6px;\n    }\n\n    &:before {\n      content: \"\";\n      position: absolute;\n      background: 0 0;\n      border: 6px solid transparent;\n      box-sizing: border-box;\n    }\n\n    // Sizing\n    &.extra-large,\n    &.large,\n    &.small,\n    &.medium {\n      white-space: normal;\n      line-height: 1.4em;\n      word-wrap: break-word;\n    }\n    &.extra-large {\n      width: 400px;\n      font-size: 1.2vh;\n    }\n    &.large {\n      width: 300px;\n    }\n    &.medium {\n      width: 150px;\n    }\n    &.small {\n      width: 80px;\n    }\n    &.preline {\n      white-space: pre-line;\n    }\n  }\n}\n","novo-slider {\n  display: block;\n  width: 100%;\n  max-width: 1000px;\n  margin: 0 auto;\n  position: relative;\n  overflow: hidden;\n\n  @for $i from 0 through 100 {\n    $offset: (#{$i * 100%});\n    $dist: (#{$i * -100%});\n\n    & > [slide=\"#{$i}\"] {\n      left: $offset;\n    }\n\n    &.slide-#{$i} {\n      .slides {\n        transform: translateX($dist);\n      }\n    }\n  }\n\n  .slides {\n    min-height: 250px;\n    transition: all 200ms ease-in-out;\n    width: 100%;\n    display: flex;\n    flex: 1 0 100%;\n\n    & > [slide] {\n      display: flex;\n      flex: 1 0 100%;\n      justify-content: center;\n      align-items: center;\n      flex-wrap: wrap;\n      flex-direction: column;\n    }\n\n    img {\n      display: block;\n      margin: auto;\n    }\n  }\n\n  .controls {\n    width: 100%;\n    position: absolute;\n    bottom: 10px;\n\n    button {\n      position: absolute;\n      top: 50%;\n      transform: translateY(-50%);\n\n      &[icon=\"previous\"] {\n        left: 10px;\n      }\n      &[icon=\"next\"],\n      &[icon=\"check\"] {\n        right: 10px;\n      }\n    }\n  }\n\n  .indicators {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    justify-content: center;\n    width: 100%;\n    background: rgba(#fff, 0.05);\n    padding: 20px;\n\n    .indicator-circle {\n      width: 8px;\n      height: 8px;\n      margin: 0 5px;\n      border-radius: 50%;\n      background: #d8d8d8;\n      opacity: 0.2;\n      transition: all 200ms ease-in-out;\n\n      &.active {\n        opacity: 1;\n      }\n    }\n  }\n}\n","nov